@charset "UTF-8";

/* border-box 초기화 */
*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

/* 가로모드 글시 확대 초기화 */
html {
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%
}

/* 공백 초기화 */
body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
select,
table,
td,
textarea,
th,
ul,
figure,
figcaption { 
    margin:0; 
    padding:0;
}

/* 폰트 초기화 */
body,
button,
input,
select,
table,
textarea {
    font-size: 16px;
    line-height: 1.4;
    color: #202020;
    font-family: -apple-system, BlinkMacSystemFont,"Malgun Gothic","맑은 고딕",helvetica,"Apple SD Gothic Neo",sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

/* 링크 초기화 */
a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none
}

/* 버튼 초기화 */
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer
}

/* 스타일 초기화 */
h1,
h2,
h3,
h4, 
h5,
h6 {
    font-weight: normal;
} 
ul {
    list-style: none;
}
em, address {
    font-style: normal;
}
strong {
    font-weight: normal;
}
img {
    vertical-align: top;
    width: 100%;
}
fieldset {
    border: none;
}
/* table */
table {
    border: 0;
    border-collapse: collaspe;
    border-spacing: 0;
    width: 100%;
}

/* 간격 설정 */
.mt0 {margin-top: 0 !important;}

/* 디자인 초기화 */
input,
textarea,
button,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    -o-border-radius: 0;
    border-radius: 0;
    font-size: 16px;
}


/* 스킵메뉴 */
#skip a {
    position: absolute;
    left: 10px;
    top: -92px;
    z-index: 100000;
    color: #000;
    font-size: 18px;
    padding: 10px;
    background: #fff;
    font-family: 'JSDongkang-Regular';
    transition: none;
}
#skip a:focus,
#skip a:active {
    top: 20px;
    transition: all 0.6s ease;
}

/* 글씨 감추기 */
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}

/* 스크롤바 */
::-webkit-scrollbar{width:10px; height: 10px;}
::-webkit-scrollbar-track {background-color:transparent;}
::-webkit-scrollbar-thumb {background-color:transparent; border-radius: 5px; transition: all .3s ease; } 
::-webkit-scrollbar-thumb:hover {background: #fedb01;}

